import comments from "../../components/comments.vue"
import {
	checkTopicList,
	getLikeNumber,
	topicLike,
	topicComment,
	getAllComment,
	topicReply,
	getReply
} from '../../api/api.js';
export default {
	components: {
		comments
	},
	data() {
		return {
			topicList: '',
			likeCount: "",
			commCont: 0,
			showComment: false,
			commContent: '',
			commplaceholder: '請輸入評論內容',
			likeimg: "../../static/img/topic/like.svg",
			unlikeimg: "../../static/img/topic/fabulous.svg",
			islike: false,
			commList: [],
			replyList: '',
			addType: '',
			topicId: '',
			topicRid: '',
			commIndex: '',
			height: "",
			pageSize: 10,
			loadMoreText: "上拉加載更多",
			showLoadMore: false,
		}
	},
	created: function() {
		const res = uni.getSystemInfoSync();
		this.height = res.windowHeight;

	},
	methods: {
		toUser(id) {
			uni.navigateTo({
				url: '/pages/daily/userInfo?uid=' + id
			});
		},
		publishDyn() {
			uni.navigateTo({
				url: '../publish/publish?tid=' + 1
			});
		},
		getTopicList() {
			checkTopicList(1, 0).then(data => {
				this.topicList = data
				console.log(data)
			})

		},
		usTopicLike(id, index) {
			if (this.topicList[index].isLike !== 1) {
				this.topicList[index].isLike = 1
				this.topicList[index].likeCount += 1
				console.log(this.topicList[1].isLike)
				topicLike(id).then(data => {
					getLikeNumber(id).then(data => {
						console.log(data)
						this.likeCount = data.likeCount
					})
				});
				console.log(index)

			}

		},
		showComm(id, index) {
			(this.showComment == true) ? (this.showComment = false) : (this.showComment = true);
			let cid
			getAllComment(id).then(data => {
				this.commList = data
				this.commCont = data.length
				this.replyList = data.reply
				console.log(data)
			})
			this.topicId = id
			this.commIndex = index

		},
		hidComm() {
			(this.showComment == true) ? (this.showComment = false) : (this.showComment = true);
			this.commContent = ''
			this.commList = ''
			this.commCont = 0
		},
		addComm() {
			if (this.commContent == "") {
				uni.showToast({
					icon: 'none',
					title: '請輸入評論'
				});
			} else if (this.addType == 'reply') {
				topicReply(this.topicRid, this.commContent).then(data => {
					console.log("reply")
					getAllComment(this.topicId).then(data => {
						this.commList = data
						console.log(this.commList)
						this.commCont = data.length
						this.addType = ''; // 重置发送按钮类型
						uni.showToast({
							icon: 'none',
							title: '回復成功'
						})
						this.commContent = ''
						this.commplaceholder = '請輸入評論內容'; // 清除占位符
						this.topicList[this.commIndex].commCount += 1
					})
				})
			} else {
				topicComment(this.topicId, this.commContent).then(data => {
					getAllComment(this.topicId).then(data => {
						this.commList = data
						this.commCont = data.length
					})
				})
				uni.showToast({
					icon: 'none',
					title: '評論成功'
				})
				this.commContent = ''
				this.addType = ''; // 重置发送按钮类型
				this.commplaceholder = '請輸入評論內容'; // 清除占位符
				this.topicList[this.commIndex].commCount += 1
			}

		},
		replyComm(item) {
			if (item[2] != uni.getStorageSync('USERS_KEY').id) {
				this.commplaceholder = '@' + item[0];
				this.addType = 'reply';
				this.topicRid = item[1]
			} else {
				uni.showToast({
					icon: 'none',
					title: '不能自己回復自己哦'
				})
			}
		},

	},
	onPullDownRefresh() {
		console.log("onPullDownRefresh")
		setTimeout(function() {
			uni.startPullDownRefresh()
		}, 300)
	},
	onReachBottom() {
		checkTopicList(1, this.pageSize).then(data => {
			this.pageSize += 10;
			if (!data) {
				this.showLoadMore = true
				this.loadMoreText = "暫無更多數據!"
				return
			}
			this.showLoadMore = true;
			this.topicList = this.topicList.concat(data)
		})
	},
	onLoad() {
		this.getTopicList()
		uni.getSystemInfo({
			success: function(res) {
				console.log(res.windowHeight);
				this.height = res.windowHeight;
				let view = uni.createSelectorQuery().select(".page")
				view.boundingClientRect(data => {
					console.log(data)
				})
			}
		});

	},


	onShow() {
		this.getTopicList()
	}
}
